<template>
  <div class="home">
     <h2>这是我的首页...</h2>
     <div class="text" ref="showText"></div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      containerText: ''
    }
  },
  computed: {
  },
  created () {
    // this.init()
  },
  mounted () {
    this.init()
  },
  updated () {
  },
  methods: {
    init () {
      let str = `<p>生活是种延绵不绝的渴望，渴望不断的上升，变得伟大而高贵...</p>
                 <p>我叫<span style="color:red;">靳晓煌</span>，年龄26，是一名<span style="color:red;">前端开发工程师</span>，2015年毕业于太原大学计算机工程系，三年前端开发经验，熟悉MV*开发，深谙自动化/模块化开发之道，高效的自学能力，具备独立分析解决问题能力，强烈的自我驱动力，高效的执行力，代码强迫症患者...</p>
                 <p>我的邮箱：j8687283@163.com,电话：<a href="tel:18435105873">18435105873</a>。</p>`
      let i = 0
      this.write(str, i)
    },
    write (str, i) {
      if (i < str.length) {
        this.$refs.showText.innerHTML = str.slice(0, i++) + ' _ '
        setTimeout(e => {
          this.write(str, i)
        }, 150)
      } else {
        this.$refs.showText.innerHTML = str.slice(0, i++)
      }
    }
  },
  watch: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
.home{
    padding:15px;
    .text{
        font-size: 14px;
        text-indent: 20px;
        padding:10px 0;
        line-height: 25px;
        color: #333;
    }
}
</style>



